<template>
  <div id="info">
    <div style="width: 100% ;height:44px;">
      <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-top:20px;">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>区块链基本信息</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="table-con">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="name" label="组织" width="180"></el-table-column>
        <el-table-column prop="type" label="节点类型" width="180"></el-table-column>
        <el-table-column prop="domain_name" label="节点域名"></el-table-column>
        <el-table-column prop="port" label="端口"></el-table-column>
        <el-table-column  label="状态">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.health_check.status}}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  name: "info",
  data() {
    return {
      tableData: []
    };
  },

  created() {
    this.$axios.get("http://127.0.0.1:8080/api/healthz").then(res => {
      if (res.data.return_code === 200) {
       this.tableData = res.data.data ;
      }
    });
  },
  components: {}
};
</script>


<style lang="less" scoped>
.table-con {
  margin-left: 150px;
  margin-right: 150px;
}
.top-con {
  width: 100%;
  height: 180px;
  display: flex;
  margin-bottom: 40px;
}
.left-con {
  width: 35%;
  height: 100%;
}
.right-con {
  width: 40%;
  height: 100%;
}
.row {
  width: 100%;
  height: 60px;
  line-height: 60px;
}
</style>
